<script>
import SoccerPlayer from './components/SoccerPlayer.vue'
import { VueDraggableNext } from 'vue-draggable-next'
import TeamRadarChart from './components/TeamRadarChart.vue'
import html2canvas from 'html2canvas'

export default {
  name: 'App',
  components: {
    SoccerPlayer,
    draggable: VueDraggableNext,
    TeamRadarChart
  },
  data() {
    return {
      players: [
        { id: 1, name: 'Marco', skills: { attack: 4, defense: 5, technic: 2, speed: 3, resistance: 2 } },
        { id: 2, name: 'Lollo', skills: { attack: 5, defense: 2, technic: 5, speed: 5, resistance: 5 } },
        { id: 3, name: 'Peddy', skills: { attack: 4, defense: 3, technic: 5, speed: 4, resistance: 4 } },
        { id: 4, name: 'Miki', skills: { attack: 3, defense: 5, technic: 3, speed: 3, resistance: 4 } },
        { id: 5, name: 'Vascio', skills: { attack: 4, defense: 1, technic: 1, speed: 1, resistance: 2 } },
        { id: 6, name: 'Spaggio', skills: { attack: 2, defense: 5, technic: 4, speed: 3, resistance: 5 } },
        { id: 7, name: 'Daut', skills: { attack: 2, defense: 5, technic: 2, speed: 3, resistance: 4 } },
        { id: 8, name: 'Huasc', skills: { attack: 3, defense: 2, technic: 5, speed: 4, resistance: 4 } },
        { id: 9, name: 'Alan', skills: { attack: 3, defense: 4, technic: 5, speed: 4, resistance: 4 } },
        { id: 10, name: 'Dave', skills: { attack: 5, defense: 2, technic: 5, speed: 4, resistance: 5 } },
        { id: 11, name: 'Matte', skills: { attack: 4, defense: 2, technic: 4, speed: 3, resistance: 4 } },
        { id: 12, name: 'Bafo', skills: { attack: 3, defense: 3, technic: 2, speed: 3, resistance: 4 } },
        { id: 13, name: 'Beppe', skills: { attack: 2, defense: 4, technic: 3, speed: 2, resistance: 4 } },
        { id: 14, name: 'Alex', skills: { attack: 4, defense: 3, technic: 4, speed: 4, resistance: 3 } },
        { id: 15, name: 'Filo', skills: { attack: 4, defense: 3, technic: 4, speed: 4, resistance: 4 } },
        // Add more players as needed
      ],
      mainList: [],
      team1: [],
      team2: [],
      selectedPlayer: null,
      emptyPlayer: { name: '', skills: { attack: 0, defense: 0, technic: 0, speed: 0, resistance: 0 } },
      showTeamsModal: false,
      showFilterModal: false,
      filterSelectedIds: []
    }
  },
  computed: {
    // provide a safe base URL for public/ assets (Vue CLI replacement)
    baseUrl() {
      return (typeof process !== 'undefined' && process.env && process.env.BASE_URL) ? process.env.BASE_URL : '/'
    },
    // disable drag-and-drop on small screens using Vuetify breakpoint helper
    isMobile() {
      return window.matchMedia && window.matchMedia('(max-width: 600px)').matches
    },
    // order of ids that are currently visible according to the active filter.
    // used to compute fresh numbering without changing the v-for source array (mainList),
    // so draggable still binds to mainList.
    filteredOrder() {
      return this.mainList
        .filter(p => this.passesFilter(p))
        .map(p => p.id)
    },
    // tri-state master checkbox helpers
    masterChecked: {
      get() {
        if (!this.mainList || this.mainList.length === 0) return false
        return this.filterSelectedIds.length === this.mainList.length
      },
      set(value) {
        if (value) {
          this.selectAllFilters()
        } else {
          this.clearFilters()
        }
      }
    },
    masterIndeterminate() {
      return this.filterSelectedIds.length > 0 && this.filterSelectedIds.length < this.mainList.length
    }
  },
  created() {
    this.mainList = [...this.players]
    if (this.mainList.length > 0) {
      this.selectedPlayer = this.mainList[0]
    }
  },
  methods: {
    selectPlayer(player) {
      this.selectedPlayer = player
    },
    onDragEnd(event) {
      // Ensure a player is only in one list at a time
      this.removePlayerFromLists(event.item)
      if (event.to === this.$refs.mainList) {
        this.mainList.push(event.item)
      } else if (event.to === this.$refs.team1) {
        this.team1.push(event.item)
      } else if (event.to === this.$refs.team2) {
        this.team2.push(event.item)
      }
    },
    removePlayerFromLists(player) {
      this.mainList = this.mainList.filter(p => p.id !== player.id)
      this.team1 = this.team1.filter(p => p.id !== player.id)
      this.team2 = this.team2.filter(p => p.id !== player.id)
    },
    movePlayerToTeam(player, teamNumber) {
      // Remove player from any list they might be in, then add to the target team
      this.removePlayerFromLists(player)
      if (teamNumber === 1) {
        this.team1.push(player)
      } else if (teamNumber === 2) {
        this.team2.push(player)
      }
    },
    removeFromTeam(player) {
      // Remove player from teams and return to main list
      this.removePlayerFromLists(player)
      this.mainList.push(player)
    },
    clearTeam1() {
      this.mainList.push(...this.team1)
      this.team1 = []
    },
    clearTeam2() {
      this.mainList.push(...this.team2)
      this.team2 = []
    },
    // filter helpers for Main List
    passesFilter(player) {
      if (!this.filterSelectedIds || this.filterSelectedIds.length === 0) return true
      return this.filterSelectedIds.includes(player.id)
    },
    selectAllFilters() {
      this.filterSelectedIds = this.mainList.map(p => p.id)
    },
    clearFilters() {
      this.filterSelectedIds = []
    },
    async captureAndShare() {
    try {
      await this.$nextTick()

      const el = document.getElementById('teams-overview') || this.$refs.teamsOverview
      if (!el || !document.body.contains(el)) {
        throw new Error('Teams overview element is not attached to document')
      }

      // Prefer the element's computed background; fall back to white
      let bg = window.getComputedStyle(el).backgroundColor
      if (!bg || bg === 'transparent' || bg === 'rgba(0, 0, 0, 0)') {
        bg = '#ffffff'
      }

      const canvas = await html2canvas(el, {
        useCORS: true,
        backgroundColor: bg, // fill canvas with the visible background color
        scale: Math.min(2, window.devicePixelRatio || 1)
      })

      // If some elements still render transparent, ensure canvas has opaque background:
      // (optional extra safeguard) draw a solid rect behind content
      const ctx = canvas.getContext('2d')
      if (ctx) {
        ctx.globalCompositeOperation = 'destination-over'
        ctx.fillStyle = bg
        ctx.fillRect(0, 0, canvas.width, canvas.height)
        ctx.globalCompositeOperation = 'source-over'
      }

      const blob = await new Promise(resolve => canvas.toBlob(resolve, 'image/png'))
      if (!blob) throw new Error('Failed to generate image')

      const file = new File([blob], 'teams-overview.png', { type: 'image/png' })

      if (navigator.canShare && navigator.canShare({ files: [file] })) {
        await navigator.share({
          files: [file],
          title: 'Teams overview',
          text: 'Here is the teams overview'
        })
        return
      }

      const url = URL.createObjectURL(blob)
      window.open(url, '_blank')
      const a = document.createElement('a')
      a.href = url
      a.download = 'teams-overview.png'
      a.click()
      setTimeout(() => URL.revokeObjectURL(url), 10000)
    } catch (err) {
      console.error('Share failed', err)
    }
  },
  }
}
</script>

