<template>
  <div class="chart-container">
    <RadarChart :data="chartData" :options="chartOptions" />
  </div>
</template>

<script>
import { Radar } from 'vue-chartjs'
import { Chart as ChartJS, registerables } from 'chart.js'
ChartJS.register(...registerables)

export default {
  name: 'SoccerPlayer',
  components: {
    RadarChart: Radar
  },
  props: {
    player: Object,
    showLegend: { type: Boolean, default: true }
  },
  computed: {
    chartData() {
      return {
        labels: ['Att', 'Dif', 'Tec', 'Vel', 'Res'],
        datasets: [
          {
            label: this.player.name,
            data: [
              this.player.skills.attack,
              this.player.skills.defense,
              this.player.skills.technic,
              this.player.skills.speed,
              this.player.skills.resistance
            ],
            backgroundColor: 'rgba(24,103,192, 0.5)',
            borderColor: 'rgb(24,103,192)',
            borderWidth: 1,
          }
        ]
      }
    },
    chartOptions() {
      const maxSkillValue = Math.max(
        this.player.skills.attack,
        this.player.skills.defense,
        this.player.skills.technic,
        this.player.skills.speed,
        this.player.skills.resistance
      )
      return {
        responsive: true,
        maintainAspectRatio: false,
        animation: false,
        plugins: {
          legend: { display: this.showLegend },
        },
        color: 'rgba(0, 0, 0, 1)',
        scales: {
          r: {
            min: 0,
            max: Math.ceil(maxSkillValue / 5) * 5, // Adjust max value dynamically
            ticks: {
              stepSize: 1,
              display: false,
              color: 'rgba(0, 0, 0, 1)'
            },
            grid: {
              color: 'rgba(0, 0, 0, 0.3)'
            },
            angleLines: {
              color: 'rgba(0, 0, 0, 0.3)'
            },
            pointLabels: {
              color: 'rgba(0, 0, 0, 1)'
            }
          }
        }
      }
    }
  }
}
</script>

<style>
.chart-container {
  position: relative;
  width: 100%;
  height: 100%;
}
</style>