<template>
    <div class="chart-container">
      <RadarChart :data="chartData" :options="chartOptions" />
    </div>
  </template>
  
  <script>
  import { Radar } from 'vue-chartjs'
  import { Chart as ChartJS, registerables } from 'chart.js'
  ChartJS.register(...registerables)
  
  export default {
    name: 'TeamRadarChart',
    components: {
      RadarChart: Radar
    },
    props: {
      team1: Array,
      team2: Array,
      showLegend: { type: Boolean, default: true }
    },
    computed: {
      chartData() {
        const totalSkillsTeam1 = this.calculateTotalSkills(this.team1)
        const totalSkillsTeam2 = this.calculateTotalSkills(this.team2)
        return {
          labels: ['Att', 'Dif', 'Tec', 'Vel', 'Res'],
          datasets: [
            {
              label: 'Neri',
              data: [
                totalSkillsTeam1.attack,
                totalSkillsTeam1.defense,
                totalSkillsTeam1.technic,
                totalSkillsTeam1.speed,
                totalSkillsTeam1.resistance
              ],
              backgroundColor: 'rgba(0, 0, 0, 0.5)',
              borderColor: 'rgba(0, 0, 0, 1)',
              borderWidth: 1
            },
            {
              label: 'Bianchi',
              data: [
                totalSkillsTeam2.attack,
                totalSkillsTeam2.defense,
                totalSkillsTeam2.technic,
                totalSkillsTeam2.speed,
                totalSkillsTeam2.resistance
              ],
              backgroundColor: 'rgba(255, 255, 255, 0.8)',
              borderColor: 'rgba(50, 50, 50, 1)',
              borderWidth: 1
            }
          ]
        }
      },
      chartOptions() {
        return {
          responsive: true,
          maintainAspectRatio: false,
          plugins: { 
            legend: { 
              display: this.showLegend 
            } 
          },
          color: 'rgba(0, 0, 0, 1)',
          scales: {
            r: {
              min: 0,
              ticks: {
                stepSize: 1,
                display: false,
                color: 'rgba(0, 0, 0, 1)'
              },
              grid: {
                color: 'rgba(0, 0, 0, 0.3)'
              },
              angleLines: {
                color: 'rgba(0, 0, 0, 0.3)'
              },
              pointLabels: {
                color: 'rgba(0, 0, 0, 1)'
              }
            }
          }
        }
      }
    },
    methods: {
      calculateTotalSkills(team) {
        return team.reduce((acc, player) => {
          acc.attack += player.skills.attack
          acc.defense += player.skills.defense
          acc.technic += player.skills.technic
          acc.speed += player.skills.speed
          acc.resistance += player.skills.resistance
          return acc
        }, { attack: 0, defense: 0, technic: 0, speed: 0, resistance: 0 })
      }
    }
  }
  </script>
  
  <style scoped>
  .chart-container {
    position: relative;
    width: 100%;
    height: 100%;
  }
  </style>